.modal {
  opacity: 0;
  visibility: hidden;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  transition: opacity 0.25s ease;
  z-index: 2;
}

.modal.open {
  opacity: 1;
  visibility: visible;
}

.modal-inner {
  transition: top 0.25s ease;
  max-width: 350px;
  margin: auto;
  overflow: auto;
  background: #fff;
  border-radius: 5px;
  position: relative;
}

.modal-close {
  margin: 20px;
  width: 20px;
  position: absolute;
  right: 10px;
  top: 10px;
  cursor: pointer;
}

svg {
  display: block;
}

.close-x {
  stroke: gray;
  fill: transparent;
  stroke-linecap: round;
  stroke-width: 5;
}

@media screen and (max-width: 768px) {
  .modal-inner {
    width: 90%;
    height: 90%;
    box-sizing: border-box;
  }
}

.switch {
  z-index: 0;
  position: relative;
}

.switch > input {
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  z-index: -1;
  position: absolute;
  right: 6px;
  top: -8px;
  display: block;
  margin: 0;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  outline: none;
  opacity: 0;
  transform: scale(1);
  pointer-events: none;
  transition: opacity 0.3s 0.1s, transform 0.2s 0.1s;
}

/* Span */
.switch > span {
  display: inline-block;
  width: 100%;
  cursor: pointer;
}

/* Track */
.switch > span::before {
  content: '';
  float: right;
  display: inline-block;
  margin: 5px 0 5px 10px;
  border-radius: 7px;
  width: 36px;
  height: 14px;
  background-color: rgba(0, 0, 0, 0.38);
  vertical-align: top;
  transition: background-color 0.2s, opacity 0.2s;
}

.switch > span::after {
  content: '';
  position: absolute;
  top: 2px;
  right: 16px;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  background-color: white;
  box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14),
    0 1px 5px 0 rgba(0, 0, 0, 0.12);
  transition: background-color 0.2s, transform 0.2s;
}

.switch > input:checked {
  right: -10px;
  background-color: rgb(0, 188, 212);
}

.switch > input:checked + span::before {
  background-color: rgba(0, 188, 212, 0.6);
}

.switch > input:checked + span::after {
  background-color: rgb(0, 188, 212);
  transform: translateX(16px);
}

/* Hover, Focus */
.switch:hover > input {
  opacity: 0.04;
}

.switch > input:focus {
  opacity: 0.12;
}

.switch:hover > input:focus {
  opacity: 0.16;
}

.switch > input:active {
  opacity: 1;
  transform: scale(0);
  transition: transform 0s, opacity 0s;
}

.switch > input:active + span::before {
  background-color: rgba(0, 188, 212, 0.6);
}

.switch > input:checked:active + span::before {
  background-color: rgba(0, 0, 0, 0.38);
}

.switch > input:disabled {
  opacity: 0;
}

.switch > input:disabled + span {
  color: black;
  opacity: 0.38;
  cursor: default;
}

.switch > input:disabled + span::before {
  background-color: rgba(0, 0, 0, 0.38);
}

.switch > input:checked:disabled + span::before {
  background-color: rgba(0, 188, 212, 0.6);
}

